.bg-mob-header {
    background-image: linear-gradient(76deg, #9d4eb3 7%, #6c37b1 35.92%, #310877 85.69%);
  position: relative;
  filter: drop-shadow(0 4px 30px #d180ee33);
  border-radius: 30px 30px 0 0 !important;
  border-top: 2px solid #fff6;
  border-bottom: 2px solid #fff6;
}
.bg-mob-header svg {
    color: #ff8f1f;
}
.round-logo {
    background: #ff8f1f;
    border: 3px solid #fff;
}


@media(min-width: 320px) and (max-width: 767px) {
    .round-logo {
        position: absolute;
        z-index: -1;
        background-image: linear-gradient(76deg, #9d4eb3 7%, #6c37b1 35.92%, #310877 85.69%);
    border: 2px solid #fff6;
    width: 10rem !important;
    height: 10rem !important;
    }
    .rnd-logo:after {
        content: "";
        width: 104px;
        height: 10px;
        position: absolute;
        top: -2px;
        left: 135px;
        -webkit-backdrop-filter: blur(20px);
                backdrop-filter: blur(20px);
        background: #310877;
    }
}